.modal {
    display: none;
}
.type17 {
    padding: 20px 0;
}
.type17 .line-product {
    display: table-row;
  }
  .type17 .line-product .left-line {
    display: table-cell;
    vertical-align: middle;
    width: 50%;
    padding-right: 20px;
  }
  .type17 .line-product .right-line {
    display: table-cell;
    vertical-align: middle;
    width: 50%;
    padding-left: 20px;
  }
  .type17 .line-product .left-line:after,
  .type17 .line-product .right-line:after {
    background-color: #d45a5a;
    content: "";
    width: 100%;
    display: inline-block;
    height: 1px;
    vertical-align: middle;
  }
  .type17 .line-product .title {
    white-space: nowrap;
    margin-bottom: 0;
    text-align: center;
    font-weight: bold;
    color: #d45a5a;
    font-size: 25px;
  }
  .type17 .manex {
    padding-top: 20px;
  }
  .type17 .line-range .product-sale {
    font-family: Raleway, sans-serif;
    text-align: center;
    border: 1px solid rgba(0,0,0,0.1);
    overflow: hidden;
    position: relative;
    z-index: 1;
  }
  .type17 .line-range .product-sale .product-image {
    position: relative;
    transition: all .3s ease-out 0s;
  }
  .type17 .line-range .product-sale .product-image a {
    display: block;
  }
  .type17 .line-range .product-sale .product-image img {
    width: 100%;
    height: auto;
  }
  .type17 .line-range .product-sale .pic-1 {
    opacity: 1;
    transition: all .3s ease-out 0s;
  }
  .type17 .line-range .product-sale .pic-2 {
    opacity: 0;
    position: absolute;
    top: 0;
    left: 0;
    transition: all .3s ease-out 0s;
  }
  .type17 .line-range .product-sale .social {
    width: 150px;
    padding: 0;
    margin: 0;
    list-style: none;
    opacity: 0;
    transform: translateY(-50%) translateX(-50%);
    position: absolute;
    top: 60%;
    left: 50%;
    z-index: 1;
    transition: all .3s ease-out 0s;
  }
  .type17 .line-range .product-sale .social li {
    display: inline-block;
  }
  .type17 .line-range .product-sale .social li a {
    color: #fff;
    background-color: #333;
    font-size: 16px;
    line-height: 40px;
    text-align: center;
    height: 40px;
    width: 40px;
    margin: 0 2px;
    display: block;
    position: relative;
    transition: all .3s ease-out 0s;
  }
  .type17 .line-range .product-sale .social li a:hover {
    color: #fff;
    background-color: #d45a5a;
  }
  .type17 .line-range .product-sale .social li a:after,
  .type17 .line-range .product-sale .social li a:before {
    content: attr(data-tip);
    color: #fff;
    background-color: #000;
    font-size: 12px;
    letter-spacing: 1px;
    line-height: 20px;
    padding: 1px 5px;
    white-space: nowrap;
    opacity: 0;
    transform: translateX(-50%);
    position: absolute;
    left: 50%;
    top: -30px;
  }
  .type17 .line-range .product-sale .social li a:after {
    content: '';
    height: 15px;
    width: 15px;
    border-radius: 0;
    transform: translateX(-50%) rotate(45deg);
    top: -20px;
    z-index: -1;
  }
  .type17 .line-range .product-sale .social li a:hover:after,
  .type17 .line-range .product-sale .social li a:hover:before {
    opacity: 1;
  }
  .type17 .line-range .product-sale .product-discount-label,
  .type17 .line-range .product-sale .product-new-label {
    color: #fff;
    background-color: #d45a5a;
    font-size: 12px;
    text-transform: uppercase;
    padding: 2px 7px;
    display: block;
    position: absolute;
    top: 0;
    left: 0;
  }
  .type17 .line-range .product-sale .product-discount-label {
    background-color: #333;
    font-weight: bold;
    left: auto;
    right: 0;
  }
  .type17 .line-range .product-sale .product-content {
    text-align: center;
  }
  .type17 .line-range .product-sale .product-content .title {
    font-size: 17px;
    font-weight: 400;
    letter-spacing: .5px;
    text-transform: capitalize;
    margin: 0 0 10px;
    transition: all .3s ease-out 0s;
  }
  .type17 .line-range .product-sale .product-content .price {
    color: #333;
    font-size: 17px;
    font-family: Montserrat, sans-serif;
    font-weight: 700;
    letter-spacing: .6px;
    margin-bottom: 8px;
    text-align: center;
    transition: all .3s;
  }
  .type17 .line-range .product-sale:hover .pic-2 {
    opacity: 1;
  }
  .type17 .line-range .product-sale:hover .social {
    opacity: 1;
    top: 50%;
  }
  .type17 .line-range .img-viewproduct {
    width: 100%;
  }
  .type17 .line-range .img-viewpd {
    width: 100%;
  }
  .type17 .line-range .btn-view {
    background: #fff;
    color: #d45a5a;
    border: 2px solid #d45a5a;
  }
  .type17 .line-range .product-tabnew a:hover {
    color: #fff;
  }
  .type17 .line-range .btn-view:hover {
    background: #d45a5a;
    color: #fff;
  }
  .type17 .product-sale a:hover {
    color: #d45a5a;
  }
  .type17 .product-sale:hover .product-content {
    bottom: 0;
  }
  .type17 .product-sale .title a {
    color: #828282;
    text-decoration: none;
  }
  .type17 .product-sale .title a:hover,
  .type17 .product-grid:hover .title a {
    color: #d45a5a;
  }
  .type17 .product-sale .price span {
    color: #999;
    font-size: 13px;
    font-weight: 400;
    text-decoration: line-through;
    margin-left: 3px;
    display: inline-block;
  }
  .type17 .range-row {
    padding-top: 20px;
  }
  .type17 .modal-dialog {
    max-width: 800px;
  }
  .type17 .modal-dialog .productview-title {
    color: #47a7fd;
  }
  .type17 .modal-dialog .productview-price {
    color: #000;
  }
  .type17 .modal-dialog .quantity {
    display: inline-block;
  }
  .type17 .modal-dialog .quantity .input-text.qty {
    width: 48px;
    height: 38px;
    padding: 0 5px;
    text-align: center;
    border: 0;
    background-color: transparent;
  }
  .type17 .modal-dialog .quantity.buttons_added .minus,
  .type17 .modal-dialog .quantity.buttons_added .plus {
    padding: 7px 10px;
    height: 38px;
    background: #fff;
    color: #d45a5a;
    border: 1px solid #d45a5a;
    cursor: pointer;
  }
  .type17 .modal-dialog .quantity.buttons_added .minus:hover,
  .type17 .modal-dialog .quantity.buttons_added .plus:hover {
    background: #d45a5a;
    color: #fff;
  }
  .type17 .modal-dialog .quantity.buttons_added .minus:focus,
  .type17 .modal-dialog .quantity.buttons_added .plus:focus {
    outline: none;
  }
  .type17 .modal-dialog .btn-giohang {
    display: inline;
  }
  .type17 .modal-dialog .btn-cart {
    background: #fff;
    color: #d45a5a;
    padding: 7px;
    margin-left: 10px; 
    border: 2px solid #d45a5a;
  }
  .type17 .modal-dialog .btn-cart:hover {
    background: #d45a5a;
    color: #fff;
  }
  .type17 .modal-dialog .viewproduct-bot {
    padding-top: 20px;
    padding-bottom: 15px;
  }
  .type17 .modal-dialog .share-product a {
    margin: 0 5px;
    color: #b1abab;
  }
  .type17 .modal-dialog .share-product a:hover {
    color: #d45a5a;
  }
  .type17 .modal-dialog .fab-icon-view {
    font-size: 30px;
  }
  @media screen and (max-width: 992px) {
    .range-row2 {
      padding-top: 20px;
    }
    .range-row3 {
      padding-top: 20px;
    }
  }
  